export default {
	props: {
		color: {
			type: String,
			default: '#f5f5f5'
		},
		selector: {
			type: String,
			default: 'skeleton'
		},
		loading: {
			type: String,
			default: 'spin',
			validator: function(value) {
				return ['spin', 'chiaroscuro'].indexOf(value) !== -1
			}
		}
	},
	data() {
		return {
			systemInfo: {},
			skeletonRectLists: [],
			skeletonCircleLists: []
		}
	},
	computed: {
		containerStyle() {
			return {
				width: this.systemInfo.width + 'upx',
				height: this.systemInfo.height + 'upx',
				'background-color': this.bgcolor
			}
		}
	},
	created: function() {
		//默认的首屏宽高，防止内容闪现
		const systemInfo = wx.getSystemInfoSync();
		this.systemInfo = {
			width: systemInfo.windowWidth,
			height: systemInfo.windowHeight
		}
	},
	onReady: function() {
		//绘制背景
		wx.createSelectorQuery().selectAll(`.${this.selector}`).boundingClientRect().exec((res) => {
			if (res[0].length !== 0) {
				this.systemInfo.height = res[0][0].height + res[0][0].top
			}
		});
		//绘制矩形
		this.rectHandle();
		//绘制圆形
		this.radiusHandle();
	},
	methods: {
		rectHandle() {
			//绘制不带样式的节点
			wx.createSelectorQuery().selectAll(`.${this.selector} >>> .${this.selector}-rect`).boundingClientRect().exec((res) => {
				this.skeletonRectLists = res[0]
				console.log(this.skeletonRectLists)
			})
		},
		radiusHandle() {
			const that = this;
			wx.createSelectorQuery().selectAll(`.${this.selector} >>> .${this.selector}-radius`).boundingClientRect().exec((res) => {
				this.skeletonCircleLists = res[0]
				console.log(this.skeletonCircleLists)
			})
		}
	}
}
